

































import {  
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			
		};
	},
	methods:{
		...mapMutations(['logout']),

		//退出登录
		toLogout(){
			uni.showModal({
				title: '确定要退出登录么？',
				success: (e)=>{
					if(e.confirm){
						this.logout();
						setTimeout(()=>{
							uni.navigateBack();
						}, 200)
					}
				}
			})
		},
		//switch
		switchChange(e){
			
		},

	}
}
